export class PeriodoAcademicoModel {

  public id: number = null;
  public descripcion: string = '';
  public anio: number = null;
  public activo: boolean = null;
  public fechaInicio: Date = null;
  public fechaTermino: Date = null;
  public fechaLimite: Date = null;


  constructor(fields?: {
    id?: number,
    descripcion?: string,
    anio?: number,
    activo?: boolean,
    fechaInicio?: Date,
    fechaTermino?: Date,
    fechaLimite?: Date,

  }) {
    if (fields) { Object.assign(this, fields); }
  }
}
