import { Component, OnInit } from '@angular/core';
import { TipoSolicitudService } from 'src/app/commons/services/aerc-services/tipo-solicitud.service';
import { UtilAlert } from 'src/app/commons/util/util.alert';
import { TipoSolicitudModel } from '../../../../commons/models/aerc-models/tipo-solicitud-model';
import { ToastrService, ToastrModule } from 'ngx-toastr';
import { PeriodoAcademicoModel } from '../../../../commons/models/aerc-models/periodo-academico-model';
import { PeriodoAcademicoService } from '../../../../commons/services/aerc-services/periodo-academico.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-admin-periodos',
  templateUrl: './admin-periodos.component.html',
  styles: [
  ]
})
export class AdminPeriodosComponent implements OnInit {

  public listadoPeriodos: PeriodoAcademicoModel[] = [];
  public formPeriodo: FormGroup;
  public isEdicion: boolean = false;
  public accion: 'new' | 'edit';

  public periodoTemp = new PeriodoAcademicoModel();

  constructor(
    private PeriodoAcademicoService: PeriodoAcademicoService,
    private alert: UtilAlert,

  ) { }

  //* INICIACION DE LA VENTANA
  //* ----------------------------------------------------------------------------------

  ngOnInit(): void {
    this._listarPeriodos();
  }

  ngOnDestroy(): void {
  }

  private _listarPeriodos() {
    this.PeriodoAcademicoService.findAll().subscribe((result) => {
      this.listadoPeriodos = result;
    })
  }

  public activar(periodo: PeriodoAcademicoModel) {
    let periodoActivar = { ...periodo };
    periodoActivar.activo = true;

    let listadoPeriodos = this.listadoPeriodos.filter(p => p.activo == true);
    if (listadoPeriodos.length > 0) {
      let periodoInactivar = listadoPeriodos[0];
      periodoInactivar.activo = false;
      this.guardarEstadoPeriodo(periodoInactivar);
    }

    this.guardarEstadoPeriodo(periodoActivar);
    this.alert.toastInfo("Se ha cambiado el estado del periodo");
  }

  public desactivar(periodo: PeriodoAcademicoModel) {
    let periodoInactivar = { ...periodo };
    periodoInactivar.activo = false;
    this.guardarEstadoPeriodo(periodoInactivar);
    this.alert.toastInfo("Se ha cambiado el estado del periodo");

  }

  private guardarEstadoPeriodo(periodo: PeriodoAcademicoModel) {
    this.PeriodoAcademicoService.savePeriodo(periodo).subscribe(result => {
      if (result) {
        this._listarPeriodos();
      } else {
        this.alert.toastError("Ha ocurrido un error al momento de actualizar el periodo.");
      }
    })
  }

  public guardarPeriodo(): void {
    if (this.formPeriodo.valid) {
      this.generarPeriodo();
      if (this.accion == 'new') {
        this.periodoTemp.activo = false;
      }
      this.PeriodoAcademicoService.savePeriodo(this.periodoTemp).subscribe(result => {
        if (result) {
          if (this.accion == 'new') {
            this.alert.toastInfo("Se ha guardado su periodo");
          }
          if (this.accion == 'edit') {
            this.alert.toastInfo("Se ha editado el periodo");
          }
          this._listarPeriodos();
        } else {
          this.alert.toastError("Ha ocurrido un error al momento de guardar el periodo.");
        }
      })
      this.volver();
    } else {
      this.alert.toastError("Ha ocurrido un error al momento de guardar el periodo.");
    }

  }

  public newPeriodo(accion: 'new' | 'edit', periodo: PeriodoAcademicoModel) {
    this.isEdicion = true;
    this.accion = accion;
    this.initFormPeriodo();
    if (accion == 'edit') {
      this.setFormPeriodo(periodo);
      this.periodoTemp = periodo;
    }
  }

  private initFormPeriodo(): void {
    this.formPeriodo = new FormGroup({
      descripcion: new FormControl("", [Validators.required]),
      anio: new FormControl("", [Validators.required]),
      fechaInicio: new FormControl("", [Validators.required]),
      fechaTermino: new FormControl("", [Validators.required]),
      fechaLimite: new FormControl("", [Validators.required])
    })
  }

  private setFormPeriodo(periodo: PeriodoAcademicoModel): void {
    this.formPeriodo.controls['descripcion'].setValue(periodo.descripcion);
    this.formPeriodo.controls['anio'].setValue(periodo.anio);
    this.formPeriodo.controls['fechaInicio'].setValue(periodo.fechaInicio);
    this.formPeriodo.controls['fechaTermino'].setValue(periodo.fechaTermino);
    this.formPeriodo.controls['fechaLimite'].setValue(periodo.fechaLimite);
  }

  private generarPeriodo(): void {
    this.periodoTemp.descripcion = this.formPeriodo.controls['descripcion'].value;
    this.periodoTemp.anio = this.formPeriodo.controls['anio'].value;
    this.periodoTemp.fechaInicio = this.formPeriodo.controls['fechaInicio'].value;
    this.periodoTemp.fechaTermino = this.formPeriodo.controls['fechaTermino'].value;
    this.periodoTemp.fechaLimite = this.formPeriodo.controls['fechaLimite'].value;
  }

  public volver(): void {
    this.isEdicion = false;
    this.periodoTemp = new PeriodoAcademicoModel();
  }

}





